









import { AppMain, Sidebar } from './components';
import { Component, Vue } from 'vue-property-decorator';

@Component({
  name: 'Layout',
  components: {
    AppMain,
    Sidebar
  }
})
export default class extends Vue {
  private isCollapse: boolean = false;
  private fold() {
      this.isCollapse = !this.isCollapse;
  }
}
